.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #3174fa;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/**************************** Style stuff ****************************/
body {
  background-color: black;
  display: flex;
  justify-content: space-between;
  
  
}

.share-tech-mono-regular {
  font-family: "Share Tech Mono", monospace;
  font-weight: 400;
  font-style: normal;
}

.-container {
  padding: 20px;
  font-family: 'Share Tech Mono', monospace;
  max-width: 800px;
  margin: 0 auto;
}

.headline {
  color: #ffe2b0;
}

.subtitle {
  color: pink;
}

.box-row {
  display: flex;
  gap: 24px;
  margin-top: 24px;
}

.info-box {
  background: #222;
  padding: 16px;
  border-radius: 8px;
  flex: 1;
}

.box-title {
  color: #ffe2b0;
}

.goals-list {
  list-style: none;
  padding: 0;
}
